nav{
    position: fixed;
    background-color: #25418C;
    
    height: 100vh;
    width: 100vw;
    display: flex;
    justify-content: center ;
    
    z-index: 1;
    top: -100vh;
    transition: 0.5s ease-out ;
    -webkit-transition: 0.5s ease-out;
    -o-transition: 0.5s ease-out;
}

.nav-active{
    top: 0;
    padding-top: 100px;
    z-index: 1;
}
nav ul{
    display: flex;
    flex-direction: column;
    transition: all 1s;
    -webkit-transition: all 1s ;
    -o-transition: all 1s ;
    opacity: 0;
    
}
nav .ulActive{
    display: flex;
    flex-direction: column;
    transition: all 3s;
    -webkit-transition: all 3s ;
    -o-transition: all 3s ;
    opacity: 1;
    
}
.bur{
